body {
  margin: 0;
  height: 100%;
  font-family: "ProximaNova", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 1.2;
  color: var(--mainColor);
  background-color: #fff;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100%;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1234px;
  width: 100%;
}

.rub::after {
  content: "â‚½";
  margin: 0 0 0 5px;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  z-index: -1;
}

.ibg--contain {
  background-size: contain;
}

.ibg img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

._tabs-content {
  display: none;
}

._tabs-content._active {
  display: block;
}

._tabs-item {
  cursor: pointer;
}

.os-scrollbar-track {
  width: 4px;
  background-color: #f23d18;
}

.os-scrollbar-track-off {
  background-color: #f4f4f4;
}

.os-scrollbar-handle {
  background-color: rgba(242, 61, 24, 0.5);
  height: 64px !important;
}

.os-theme-dark > .os-scrollbar > .os-scrollbar-track,
.os-theme-dark > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle,
.os-theme-light > .os-scrollbar > .os-scrollbar-track,
.os-theme-light > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle {
  border-radius: 0;
}

.os-theme-dark > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle {
  background: rgba(242, 61, 24, 0.5);
  cursor: pointer;
}

.os-theme-dark > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle:hover,
.os-theme-dark > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle:active {
  background: #f23d18;
}

.os-theme-dark > .os-scrollbar > .os-scrollbar-track {
  background: #f4f4f4;
}

.os-scrollbar-vertical {
  top: -2px;
  bottom: -2px;
  right: -4px;
}

.checkbox {
  position: relative;
  display: block;
  margin: 0 0 15px 0;
  width: 100%;
}

.checkbox:last-child {
  margin: 0;
}

.checkbox__input {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}

.checkbox__input:checked + .checkbox__text::before {
  border: 2px solid rgba(37, 37, 37, 0.5);
}

.checkbox__input:checked + .checkbox__text::after {
  opacity: 1;
  transform: scale(1);
  transition: all 0.15s cubic-bezier(0.41, 1.13, 0.79, 1.33);
  transform-origin: center;
}

.checkbox__text {
  position: relative;
  display: inline-flex;
  font-weight: 400;
  width: 100%;
  cursor: pointer;
}

.checkbox__text::before {
  content: "";
  align-items: flex-start;
  margin: -2px 12px 0 0;
  flex: 0 0 20px;
  height: 20px;
  border: 2px solid #dadada;
  left: 0;
  top: 0;
  background: #fff;
}

.checkbox__text::after {
  position: absolute;
  content: '';
  width: 12px;
  height: 12px;
  left: 4px;
  top: 2px;
  background-color: #f23d18;
  transform: scale(0.5);
  opacity: 0;
  transition: all 0.15s cubic-bezier(0.47, 1.01, 0.75, 0.97);
}

.checkbox a {
  color: #fff;
  text-decoration: underline;
}

@media (min-width: 991.98px) {
  .checkbox a:hover {
    text-decoration: none;
  }
}

.slider-nav {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.slider-nav__arrow {
  margin: 0 0 25px;
}

.slider-nav__arrow svg path {
  transition: all 0.2s;
}

.slider-nav__arrow:last-child {
  margin: 0;
}

.slider-nav__arrow:hover svg path {
  stroke: #f23d18;
}

.slider-nav__prev {
  transform: rotate(180deg);
}

.amount {
  display: flex;
  align-items: center;
  background-color: #f4f4f4;
}

.amount__input {
  max-width: 30px;
  height: 40px;
  text-align: center;
  background-color: #f4f4f4;
  outline: transparent;
  border: none;
  font-weight: 700;
  font-size: 12px;
}

.amount__button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 40px;
  cursor: pointer;
}

.amount__button--minus::after {
  display: none;
}

.amount__button::before, .amount__button::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 2px;
  background-color: #252525;
}

.amount__button::after {
  width: 2px;
  height: 10px;
}

.accordion-content {
  margin: 8px 0;
  max-width: 560px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}

@media (max-width: 600px) {
  .header {
    position: relative;
  }
  .header::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 4px;
    background: linear-gradient(135deg, #E62817 0%, #F26118 100%);
  }
}

.header__burger {
  width: 25px;
  height: auto;
}

.header__burger span {
  margin: 0 0 6px;
  width: 25px;
  height: 3px;
  background-color: #f23d18;
}

.header__burger span:last-child {
  margin: 0;
}

.top-panel {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 767.98px) {
  .top-panel {
    justify-self: flex-start;
  }
}

.top-panel__logo {
  margin: 0 26px 0 0;
  flex: 0 0 214px;
}

@media (max-width: 767.98px) {
  .top-panel__logo {
    margin: 0 auto 0 0;
  }
}

@media (max-width: 550px) {
  .top-panel__logo {
    flex-basis: 160px;
  }
}

.top-panel__address {
  margin: 0 26px 0 0;
  font-style: normal;
  color: rgba(37, 37, 37, 0.5);
  white-space: nowrap;
}

.top-panel__address span {
  position: relative;
  display: block;
  padding: 0 0 0 17px;
  color: var(--mainColor);
}

.top-panel__address span::before {
  content: "";
  position: absolute;
  left: 0;
  width: 7px;
  height: 12px;
  background: url("../images/icons/map-marker-small.svg") center no-repeat;
}

@media (max-width: 1050px) {
  .top-panel__address {
    order: 3;
    font-size: 0;
    flex: 0 0 45px;
    height: 45px;
    background: url("../images/icons/map-marker.svg") center no-repeat;
  }
  .top-panel__address span {
    display: none;
  }
}

@media (max-width: 767.98px) {
  .top-panel__address {
    margin: 0 10px 0 0;
  }
}

@media (max-width: 600px) {
  .top-panel__address {
    display: none;
  }
}

.top-panel__search-btn {
  display: none;
  flex: 0 0 45px;
  height: 45px;
  background: url("../images/icons/magnifier-big.svg") center no-repeat;
}

@media (max-width: 767.98px) {
  .top-panel__search-btn {
    display: block;
    margin: 0 10px 0 0;
    order: 2;
  }
}

.top-panel__search-btn.js-active {
  background: url("../images/icons/cancel.svg") center no-repeat;
}

.top-panel__form {
  position: absolute;
  visibility: hidden;
  left: 0;
  top: 0;
  bottom: 4px;
  width: calc(100% - 210px);
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transform: translateX(-100px);
}

@media (min-width: 769px) {
  .top-panel__form {
    display: none;
  }
}

@media (max-width: 600px) {
  .top-panel__form {
    width: calc(100% - 155px);
  }
}

@media (max-width: 400px) {
  .top-panel__form {
    width: calc(100% - 110px);
  }
}

.top-panel__form.js-opened {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
}

.top-panel__form .search-form {
  height: 100%;
}

.top-panel__form .search-form__btn {
  height: 100%;
  margin: 0 10px 0 0;
}

.top-panel__form .search-form__btn--mic {
  right: 50px;
  margin: 0 5px 0 0;
}

.top-panel__form .search-form__input {
  border: none;
  padding: 10px 95px 10px 0;
  margin: 0 0 0 -5px;
  border-right: 4px solid #f4f4f4;
  height: 100%;
  font-size: 16px;
}

.top-panel__form .search-form__input::placeholder {
  font-size: 16px;
}

.top-panel__form .search-form__icon {
  width: 28px;
  height: 28px;
}

.top-panel__search {
  flex: 1 0 auto;
  order: 2;
  margin: 0 26px 0 0;
}

@media (max-width: 767.98px) {
  .top-panel__search {
    display: none;
  }
}

.top-panel__phone {
  display: none;
  flex: 0 0 45px;
  height: 45px;
  background: url("../images/icons/smartphone.svg") center no-repeat;
}

@media (max-width: 900px) {
  .top-panel__phone {
    display: block;
    margin: 0 26px 0 0;
    order: 4;
  }
}

@media (max-width: 767.98px) {
  .top-panel__phone {
    margin: 0 10px 0 0;
  }
}

@media (max-width: 400px) {
  .top-panel__phone {
    display: none;
  }
