  .product-short .cart {
    flex-basis: 100%;
  }
}

.info-tab__header {
  position: relative;
  display: flex;
  align-items: center;
  padding: 14px 20px 16px;
  height: 70px;
  background-color: #f4f4f4;
}

@media (max-width: 600px) {
  .info-tab__header {
    flex-wrap: wrap;
    height: auto;
    padding-bottom: 0;
  }
  .info-tab__header::after {
    display: none;
  }
}

.info-tab__header::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 0;
  height: 0;
  border-top: 35px solid transparent;
  border-right: 20px solid #fff;
  border-bottom: 35px solid transparent;
}

.info-tab__header .document {
  margin: 0 40px 0 0;
}

@media (max-width: 600px) {
  .info-tab__header .document {
    margin: 0 40px 20px 0;
  }
}

.info-tab__body {
  padding: 20px 20px 0 60px;
  height: 250px;
  border: 2px solid #f4f4f4;
}

.info-tab__body .os-scrollbar-vertical {
  top: 20px !important;
  bottom: 20px !important;
  right: 20px;
}

.info-tab__control {
  width: 100%;
  height: 15px;
  background-color: #f4f4f4;
  text-align: center;
  transition: all 0.3s ease-in-out;
}

.info-tab__control:hover {
  background-color: #e7e7e7;
}

.info-tab__control span {
  position: relative;
  padding: 0 0 0 24px;
  font-size: 12px;
}

.info-tab__control span::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 12px;
  height: 2px;
  background-color: #252525;
}

.info-tab .accordion {
  position: relative;
  display: block;
  width: calc(100% - 50px);
  text-align: left;
}

.info-tab .accordion:hover span {
  color: #f23d18;
}

.info-tab .accordion::before, .info-tab .accordion::after {
  content: "";
  position: absolute;
  left: -37px;
  width: 12px;
  height: 2px;
  top: 50%;
  background-color: #252525;
  transform: translateY(-50%);
  transition: all 0.3s ease-in-out;
}

.info-tab .accordion::after {
  left: -25px;
}

.info-tab .accordion.js-active {
  color: #f23d18;
}

.info-tab .accordion.js-active span {
  border-bottom-color: #f23d18;
}

.info-tab .accordion.js-active::before, .info-tab .accordion.js-active::after {
  background-color: #f23d18;
}

.info-tab .accordion.js-active::before {
  transform: rotate(45deg) translate(10px, -8px);
  width: 7px;
}

.info-tab .accordion.js-active::after {
  transform: rotate(-45deg) translate(3px, 3px);
}

.info-tab .accordion span {
  transition: all 0.3s ease-in-out;
  border-bottom: 1px dashed rgba(37, 37, 37, 0.5);
}

.info-tab .accordion-content {
  width: calc(100% - 50px);
}

.specifications__table {
  width: 100%;
}

.specifications__table tr {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
  background-color: #f4f4f4;
  border-bottom: 2px solid #fff;
}

.specifications__label {
  margin: 0 15px 0 0;
  font-weight: 700;
  max-width: 280px;
}

.specifications__caption {
  font-weight: 700;
  margin: 0 0 5px;
}

.specifications__list {
  margin: 0 0 10px 15px;
  list-style: disc outside none;
  padding: 0 0 0 15px;
}

.specifications__text span {
  display: block;
  margin: 10px 0 0;
}

.set {
  display: flex;
  flex-wrap: wrap;
}

.set__item {
  flex: 0 1 calc(33.333% - 50px);
  margin: 10px 50px 10px 0;
  font-weight: 700;
}

.set__name {
  margin: 0 10px 0 0;
}

.set__amount {
  color: rgba(37, 37, 37, 0.5);
}
